import redirect from '../../mixins/redirect.js';
import uniLoadMore from '@dcloudio/uni-ui/lib/uni-load-more/uni-load-more.vue';
import TopDropdown from '../../components/lists/TopDropdown.vue';
import SideDrawer from '../../components/lists/SideDrawer.vue';
import GoodsLists from '../../components/lists/GoodsLists.vue';
import nullData from '../../components/lists/nullData.vue';

export default {
	name: 'lists',
	mixins: [redirect],
	components: {
		GoodsLists,
		TopDropdown,
		SideDrawer,
		uniLoadMore,
		nullData
	},
	data() {
		return {
			width: 200, //header宽度
			height: 64, //header高度
			inputTop: 0, //搜索框距离顶部距离
			arrowTop: 0, //箭头距离顶部距离
			dropScreenH: 0, //下拉筛选框距顶部距离
			attrData: [],
			attrIndex: -1,
			dropScreenShow: false,
			scrollTop: 0,
			tabIndex: 0, //顶部筛选索引
			isList: false, //是否以列表展示  | 列表或大图
			openSideDrawer: false, // 是否是打开抽屉
			sideDraweH: 0, //抽屉内部scrollview高度
			selectedName: '综合',
			orderLists: [
				{
					name: '综合',
					selected: true
				},
				{
					name: '价格升序',
					selected: false
				},
				{
					name: '价格降序',
					selected: false
				}
			],
			selectH: 0,
			goodsLists: [],
			selectIndex: 0, //选择分类
			pageIndex: 1,
			loadding: false,
			pullUpOn: true,
			loadStatus: 'loading',
			siblingList: [],
			gcId: null,
			keyword: null,
			listParam: {
				page: 1, //
				lastPage: 1, //最后一页
				keyword: ''
			},
			isShowTip: false, //数据空
			tempSide: {}
		};
	},
	onLoad: function(options) {
		if (options.keyword) {
			this.listParam.keyword = options.keyword;
			this.keyword = options.keyword;
			// 搜索关键字进来，分类筛选没有默认
			this.selectIndex = -1;
		}
		if (options.gc_id) {
			this.listParam.gc_id = options.gc_id;
			this.gcId = options.gc_id;
		}
		this.getList(true);
		this.getSiblingsList();
		let obj = {};

		obj = wx.getMenuButtonBoundingClientRect();







		uni.getSystemInfo({
			success: res => {
				this.width = obj.left || res.windowWidth;
				this.height = obj.top ? obj.top + obj.height + 8 : res.statusBarHeight + 44;
				this.inputTop = obj.top ? obj.top + (obj.height - 30) / 2 : res.statusBarHeight + 7;
				this.arrowTop = obj.top ? obj.top + (obj.height - 32) / 2 : res.statusBarHeight + 6;
				this.searchKey = options.searchKey || '';
				//略小，避免误差带来的影响
				this.dropScreenH = (this.height * 750) / res.windowWidth + this.height;
				this.sideDraweH = res.windowHeight - uni.upx2px(100) - this.height;
			}
		});
	},
	mounted() {},
	// 监听用户下拉动作，一般用于下拉刷新
	onPullDownRefresh() {
		uni.pageScrollTo({
			scrollTop: 0
		});
	},
	// 页面上拉触底事件的处理函数
	onReachBottom() {
		if (!this.pullUpOn) return;
		this.loadding = true;
		this.loadStatus = 'loading';
		if (this.listParam.page === this.listParam.lastPage) {
			this.loadding = false;
			this.pullUpOn = false;
			this.loadStatus = 'noMore';
		} else {
			this.listParam.page++;
			setTimeout(() => {
				this.getList();
				this.loadding = false;
				this.loadStatus = 'more';
			}, 500);
		}
	},
	methods: {
		getShopGoods(e) {
			this.keyword = e.detail.value;
			this.getList(true);
		},
		cleanKeyword() {
			this.$set(this.listParam, 'keyword', '');
		},
		getList(init) {
			if (init) {
				uni.pageScrollTo({
					scrollTop: 0
				});
				this.listParam.page = 1;
				this.goodsLists = [];
				this.pullUpOn = true;
			}
			this.$http({
				aurl: 'getCateList',
				data: this.listParam,
				success: res => {
					if (init) {
						this.listParam.lastPage = res.data.result.last_page;
					}
					res.data.result.data.forEach(item => {
						this.goodsLists.push(item);
					});

					if (this.goodsLists.length > 0) {
						this.isShowTip = false;
					} else {
						this.isShowTip = true;
					}

					if (res.data.result.last_page === 1) {
						this.loadding = false;
						this.pullUpOn = false;
						this.loadStatus = 'noMore';
					}
				},
				fail: err => {
					console.log(err);
				}
			});
		},
		px(num) {
			return uni.upx2px(num) + 'px';
		},
		btnDropChange(e) {
			let index = e.currentTarget.dataset.index;
		},
		btnSelected(e) {
			let index = e.currentTarget.dataset.index;
			this.selectIndex = index;
		},
		reset() {
			if (this.gcId !== null) {
				this.selectIndex = 0;
			}
		},
		btnCloseDrop() {
			this.scrollTop = 1;
			this.$nextTick(() => {
				this.scrollTop = 0;
			});
			this.dropScreenShow = false;
			this.attrIndex = -1;
		},
		btnSure() {
			if (this.selectIndex === -1) {
				uni.showToast({
					title: '请选择分类',
					icon: 'none'
				});
				return;
			} else {
				this.listParam = {};
				this.listParam.gc_id = this.siblingList[this.selectIndex].id;
				this.gcId = this.listParam.gc_id;
				this.keyword = '';
			}
			this.getList(true);
			this.btnCloseDrop();
		},
		showOrderLists() {
			this.selectH = 246;
			this.tabIndex = 0;
		},
		hideDropdownList() {
			this.selectH = 0;
		},
		selectOrderItem(e) {
			let index = Number(e.currentTarget.dataset.index);
			this.selectedName = index === 0 ? '综合' : '价格';
			this.orderLists.map((item, i) => {
				item.selected = false;
			});
			this.orderLists[index].selected = true;

			this.selectH = 0;
			this.listParam = {};
			if (this.keyword) {
				this.listParam.keyword = this.keyword;
			} else {
				this.listParam.gc_id = this.gcId;
			}
			switch (index) {
				case 0:
					//this.listParam.orderby = 'modified_time';
					break;
				case 1:
					this.listParam.orderby = 'price';
					this.listParam.direction = 'desc';
					break;
				case 2:
					this.listParam.orderby = 'price';
					this.listParam.direction = 'asc';
					break;
			}
			this.getList(true);
		},
		screen(e) {
			//筛选栏
			this.btnCloseDrop(); // 隐藏分类
			let index = Number(e.currentTarget.dataset.index);
			if (index !== 0) {
				this.selectH = 0; // 隐藏排序
			}
			if (index === 0) {
				if (this.selectH === 0) {
					this.showOrderLists();
				} else {
					this.hideDropdownList();
				}
			} else if (index === 1) {
				this.tabIndex = 1;
				if (!this.listParam.orderby) {
					this.listParam = {};
					if (this.keyword) {
						this.listParam.keyword = this.keyword;
					} else {
						this.listParam.gc_id = this.gcId;
					}
				}
				this.listParam.orderby = 'sold_quantity';
				this.listParam.direction = this.listParam.direction === 'desc' ? 'asc' : 'desc';
				this.getList(true);
			} else if (index === 2) {
				this.isList = !this.isList;
			} else if (index === 3) {
				this.openSideDrawer = true;
			} else if (index === 4) {
				this.dropScreenShow = true;
				this.$nextTick(() => {
					this.scrollTop = 0;
				});
			}
		},
		closeSideDrawer() {
			this.openSideDrawer = false;
		},
		sure(param) {
			this.openSideDrawer = false;
			if (JSON.stringify(this.tempSide) !== JSON.stringify(param)) {
				if (JSON.stringify(this.tempSide) == '{}') {
					this.tempSide = param;
					return;
				}
				this.tempSide = param;
				this.listParam.start_price = param.ps;
				this.listParam.stop_price = param.pe;
				this.getList(true);
			}
		},
		toBack() {
			if (this.openSideDrawer) {
				this.closeSideDrawer();
			} else {
				uni.navigateBack();
			}
		},
		selectItem(param) {
			console.log(111, param);
		},
		getSiblingsList() {
			this.$http({
				aurl: 'getSiblingsList',
				data: {
					gc_id: this.gcId
				},
				success: res => {
					let list = res.data.result;
					if (list.length !== 0) {
						this.siblingList = list;
					}
				},
				fail: err => {
					console.log(err);
				}
			});
		}
	}
};
